import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

import {faArrowLeft} from '@fortawesome/free-solid-svg-icons'

import staticImg from '../../../Assets/Images/staticpic.png';

import './UserDetail.css';


const UserDetail = ({friends, handleClose}) =>{
	const userdata = friends[0]?.user;
    return(
    	<>
	    	<div className="userimg" style={{ backgroundImage: `url(${userdata?.userPic ? `https://demo.conitor.in:5000/${userdata.userPic}` :  staticImg}`}}>
				<span className="closeUserdetail" onClick={handleClose}><FontAwesomeIcon icon={faArrowLeft} /></span>
			</div>
			<div className="UserDetail">	
				<p className="pl-3 userName pt-3">Name: <span className="pl-2">{friends[0]?.friendname}</span></p>
				<p className="pl-3 userName">Num: <span className="pl-3">{friends[0]?.friendnum}</span></p>
				<p className="pl-3 userName">Bio: <span className="pl-3">{friends[0]?.user?.userbio}</span></p>
	    	</div>
    	</>
    )
}

export default UserDetail